let quoteSlider, ingredientSlider, ingredientThumbs, productSlider, productThumbs;

$(document).ready(function () {
    $(".quote-slider .swiper-container").each(function (index, element) {
        let $this = $(this);
        quoteSlider = new Swiper($this, {
            slidesPerView: 2,
            spaceBetween: 0,
            grabCursor: true,
            loop: true,
            navigation: {
                nextEl: $(".next-slide"),
                prevEl: $(".prev-slide"),
                disabledClass: 'disabled'
            },
            breakpoints: {
                0: {
                    slidesPerView: 1,
                    // spaceBetween: 15,
                },
                630: {
                    slidesPerView: 2,
                    centeredSlides: false,
                    // spaceBetween: 15,
                },
                992: {
                    slidesPerView: 3,
                    centeredSlides: true,
                    // spaceBetween: 20,
                },
            }
        });
    });

    ingredientThumbs = new Swiper('.ingredients-slider .thumbs-slider .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 15,
        effect: 'fade',
        watchSlidesVisibility: true,
        grabCursor: false,
        loop: false,
        touchRatio: 0,
        pagination: {
            el: '.pagination',
            type: 'fraction',
            currentClass: 'pagination-current',
            totalClass: 'pagination-total',
            renderFraction: function (currentClass, totalClass) {
                return '<p><span class="' + currentClass + '"></span> of <span class="' + totalClass + '"></span></p>';
            }
        },

    });

    ingredientSlider = new Swiper('.ingredients-slider .main-slider .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 15,
        watchSlidesVisibility: true,
        loop: false,
        slideToClickedSlide: true,
        thumbs: {swiper: ingredientThumbs},
        // lazy: true

        navigation: {
            nextEl: '.next-slide',
            prevEl: '.prev-slide',
            disabledClass: 'disabled'
        }
    });




    productThumbs = new Swiper('.product-slider .thumbs-slider .swiper-container', {
        slidesPerView: 'auto',
        spaceBetween: 20,
        direction: 'vertical',
        watchSlidesVisibility: true,
        grabCursor: false,
        loop: false,
        touchRatio: 0,
    });

    productSlider = new Swiper('.product-slider .main-slider .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 15,
        watchSlidesVisibility: true,
        loop: false,
        slideToClickedSlide: true,
        thumbs: {swiper: productThumbs},
    });

});